// src/app/signup/page.tsx
"use client";

import React, { useState } from "react";
import { auth, googleProvider } from "@/firebase/clientApp";
import { signInWithPopup, createUserWithEmailAndPassword, signInWithEmailAndPassword, signOut } from "firebase/auth";
import GoogleButton from "@/components/auth/GoogleButton";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Image from "next/image";
import { useRouter } from "next/navigation";

export default function Page() {
  const [mode, setMode] = useState<"register" | "login">("register");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [form, setForm] = useState({ name: "", email: "", password: "" });
  const router = useRouter();

  async function handleGoogleSignIn() {
    setLoading(true);
    setError(null);
    try {
      await signInWithPopup(auth, googleProvider);
      // on success redirect or update UI
      router.push("/dashboard"); // change to where you want to send users
    } catch (err: any) {
      setError(err.message || "Google sign-in failed.");
    } finally {
      setLoading(false);
    }
  }

  async function handleRegister(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setError(null);
    try {
      const userCred = await createUserWithEmailAndPassword(auth, form.email, form.password);
      // Optionally update displayName using updateProfile if needed
      router.push("/dashboard");
    } catch (err: any) {
      setError(err.message || "Registration failed.");
    } finally {
      setLoading(false);
    }
  }

  async function handleLogin(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setError(null);
    try {
      await signInWithEmailAndPassword(auth, form.email, form.password);
      router.push("/dashboard");
    } catch (err: any) {
      setError(err.message || "Login failed.");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-white">
      <div className="w-full max-w-6xl grid grid-cols-1 md:grid-cols-2 gap-8 p-8">
        {/* LEFT - art and permission cards */}
        <div className="hidden md:flex flex-col justify-center p-8 rounded-2xl" style={{ background: 'linear-gradient(120deg, #0f172a, #07102a)' }}>
          <div className="mb-6">
            {/* replace with your image or local asset */}
            <Image src="/hero-art.jpg" alt="Art" width={520} height={420} className="rounded-lg object-cover" />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="bg-black/60 text-white rounded-lg p-4">
              <h4 className="font-semibold mb-1">Securely verify your identity</h4>
              <p className="text-sm opacity-70">We will only ask for what's needed.</p>
            </div>
            <div className="bg-black/60 text-white rounded-lg p-4">
              <h4 className="font-semibold mb-1">Retrieve your email info</h4>
              <p className="text-sm opacity-70">Used for account recovery.</p>
            </div>
            <div className="bg-black/60 text-white rounded-lg p-4">
              <h4 className="font-semibold mb-1">Access basic profile</h4>
              <p className="text-sm opacity-70">Display name and avatar.</p>
            </div>
            <div className="bg-black/60 text-white rounded-lg p-4">
              <h4 className="font-semibold mb-1">Maintain session</h4>
              <p className="text-sm opacity-70">Keep you signed in securely.</p>
            </div>
          </div>
        </div>

        {/* RIGHT - auth UI */}
        <div className="flex items-center justify-center">
          <Card className="w-full max-w-md">
            <CardContent>
              <h3 className="text-xs uppercase text-center tracking-wider mb-4">Log in to your app</h3>

              <div className="flex justify-center mb-4">
                <GoogleButton onClick={handleGoogleSignIn} />
              </div>

              <div className="text-center text-xs text-gray-400 mb-4">
                or continue with email
              </div>

              {mode === "register" ? (
                <form onSubmit={handleRegister} className="space-y-4">
                  <div>
                    <label className="block text-sm mb-1">Name</label>
                    <input required value={form.name} onChange={(e) => setForm(f => ({...f, name: e.target.value}))}
                      className="w-full px-3 py-2 rounded-lg border" />
                  </div>
                  <div>
                    <label className="block text-sm mb-1">Email</label>
                    <input required type="email" value={form.email} onChange={(e) => setForm(f => ({...f, email: e.target.value}))}
                      className="w-full px-3 py-2 rounded-lg border" />
                  </div>
                  <div>
                    <label className="block text-sm mb-1">Password</label>
                    <input required type="password" value={form.password} onChange={(e) => setForm(f => ({...f, password: e.target.value}))}
                      className="w-full px-3 py-2 rounded-lg border" />
                  </div>

                  <div>
                    <Button type="submit" className="w-full">{loading ? "Creating..." : "Create account"}</Button>
                  </div>

                  <div className="text-center text-sm">
                    <button type="button" onClick={() => { setMode("login"); setError(null); }} className="underline">
                      Already have an account? Sign in
                    </button>
                  </div>
                </form>
              ) : (
                <form onSubmit={handleLogin} className="space-y-4">
                  <div>
                    <label className="block text-sm mb-1">Email</label>
                    <input required type="email" value={form.email} onChange={(e) => setForm(f => ({...f, email: e.target.value}))}
                      className="w-full px-3 py-2 rounded-lg border" />
                  </div>
                  <div>
                    <label className="block text-sm mb-1">Password</label>
                    <input required type="password" value={form.password} onChange={(e) => setForm(f => ({...f, password: e.target.value}))}
                      className="w-full px-3 py-2 rounded-lg border" />
                  </div>

                  <div>
                    <Button type="submit" className="w-full">{loading ? "Signing in..." : "Sign in"}</Button>
                  </div>

                  <div className="text-center text-sm">
                    <button type="button" onClick={() => { setMode("register"); setError(null); }} className="underline">
                      New here? Create account
                    </button>
                  </div>
                </form>
              )}

              {error && <p className="text-red-600 text-sm mt-3 text-center">{error}</p>}

              <div className="mt-4 text-center">
                <Button onClick={() => { signOut(auth); alert("Signed out (demo)"); }} className="bg-gray-200 text-black">Sign out</Button>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
